body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  transition: background-color 0.3s, color 0.3s;
}

body[data-theme='light'] {
  background-color: #ffffff;
  color: #000000;
}

body[data-theme='dark'] {
  background-color: #1a1a1a;
  color: #ffffff;
}

nav ul {
  display: flex;
  list-style-type: none;
  padding: 0;
}

nav ul li {
  margin-right: 1rem;
}

nav ul li a {
  text-decoration: none;
  color: inherit;
}
